import { Component, OnInit, ViewChild, Inject, ChangeDetectorRef } from '@angular/core';
import { MatTableDataSource, MatSort, MatPaginator, MatDialog, MatDialogConfig } from '@angular/material';
import { Router } from '@angular/router';
import { OnBoardingFormComponent } from '../on-boarding/on-boarding-form/on-boarding-form.component';
import { OnBoardingViewComponent } from '../on-boarding/on-boarding-view/on-boarding-view.component';
import { ConfirmDialogService } from 'src/app/services/confirm-dailog/confirm-dialog.service';

@Component({
  selector: 'app-students-list',
  templateUrl: './students-list.component.html',
  styleUrls: ['./students-list.component.less']
})
export class StudentsListComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['sname', 'mothername', 'lastclassscore', 'fathername', 'docs', 'dob', 'category', 'actions'];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  searchKey: string;
  hideLoadingData: boolean;
  hideNoData: boolean;
  checkInternationalDoc = false;
  checkDomesticDoc = false;
  checkInternationalDocView = false;
  checkDomesticDocView = false;
  checkDocs: boolean[] = [];
  categories: string[] = ['All', 'Domestic', 'International'];
  selectedCategory = 'All';
  constructor(private router: Router, private dialog: MatDialog, private dialogService: ConfirmDialogService) { }

  ngOnInit() {
    const array = localStorage.getItem('studentsList');
    console.log(JSON.parse(array));
    this.listData = new MatTableDataSource(JSON.parse(array));
    this.listData._updateChangeSubscription();
    if (this.listData.data !== null) {
      this.listData.sort = this.sort;
      this.listData.paginator = this.paginator;
      this.listData.filterPredicate = (data, filter) => {
        return this.displayedColumns.some(ele => {
          return ele !== 'actions' && data[ele].toString().toLowerCase().indexOf(filter) !== -1;
        });
      };
    }

    if (this.listData.data === null && this.hideNoData) {
      this.hideLoadingData = false;
    } else {
      this.hideLoadingData = true;
    }
    if (!(this.listData.data !== null)) {
      this.hideNoData = false;
    } else {
      this.hideNoData = true;
    }
  }
  onChange(newValue): any {
    console.log(newValue);
    this.selectedCategory = newValue;
    if (this.selectedCategory === 'All' && this.listData.data != null) {
      this.searchKey = '';
      this.applyFilter();
    } else if (this.selectedCategory === 'Domestic' && this.listData.data != null) {
      this.searchKey = 'Domestic';
      this.applyFilter();
    } else if (this.selectedCategory === 'International' && this.listData.data != null) {
      this.searchKey = 'International';
      this.applyFilter();
    } else {
      this.searchKey = '';
      this.applyFilter();
    }

  }
  onSearchClear() {
    this.searchKey = '';
    this.applyFilter();
  }
  applyFilter() {
    if (this.listData.data !== null) {
      this.listData.filter = this.searchKey.trim().toLowerCase();
    }
  }


  callingEditOnBoard(row) {
    localStorage.setItem('editCalled', JSON.stringify(true));
    delete row.docs;
    localStorage.setItem('editId', JSON.stringify(row.id));
    delete row.id;
    row.dob = new Date(row.dob);
    if (row.category === 'International') {
      // tslint:disable-next-line: prefer-for-of
      for (let i = 0; i < row.internationalDocs.length; i++) {
        this.checkDocs = row.internationalDocs;
        if (this.checkDocs[i] != null) {
          this.checkInternationalDoc = true;
          localStorage.setItem('checkInternationalDoc', JSON.stringify(this.checkInternationalDoc));
        }
      }
    } else {
      // tslint:disable-next-line: prefer-for-of
      for (let i = 0; i < row.domesticDocs.length; i++) {
        this.checkDocs = row.domesticDocs;
        if (this.checkDocs[i] != null) {
          this.checkDomesticDoc = true;
          localStorage.setItem('checkDomesticDoc', JSON.stringify(this.checkDomesticDoc));
        }
      }
    }
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '60%';
    dialogConfig.height = '85%',
    dialogConfig.data = row;
    const dialogRef = this.dialog.open(OnBoardingFormComponent, dialogConfig);
    dialogRef.componentInstance.onBoardingForm.setValue(row);
    dialogRef.afterClosed().subscribe(() => {
      const array = localStorage.getItem('studentsList');
      console.log(JSON.parse(array));
      this.listData = new MatTableDataSource(JSON.parse(array));
      this.listData._updateChangeSubscription();
      if (this.listData.data !== null) {
        this.listData.sort = this.sort;
        this.listData.paginator = this.paginator;
        this.listData.filterPredicate = (data, filter) => {
          return this.displayedColumns.some(ele => {
            return ele !== 'actions' && data[ele].toString().toLowerCase().indexOf(filter) !== -1;
          });
        };
      }
    });
  }

  callingViewOnBoard(row) {
    debugger;
    delete row.docs;
    localStorage.setItem('viewId', JSON.stringify(row.id));
    delete row.id;
    if (row.category === 'International') {
      // tslint:disable-next-line: prefer-for-of
      for (let i = 0; i < row.internationalDocs.length; i++) {
        this.checkDocs = row.internationalDocs;
        if (this.checkDocs[i] !== null && this.checkDocs[i] === true) {
          this.checkInternationalDocView = true;
          localStorage.setItem('checkInternationalDocView', JSON.stringify(this.checkInternationalDocView));
        }
      }
    } else {
      // tslint:disable-next-line: prefer-for-of
      for (let i = 0; i < row.domesticDocs.length; i++) {
        this.checkDocs = row.domesticDocs;
        if (this.checkDocs[i] !== null && this.checkDocs[i] === true ) {
          this.checkDomesticDocView = true;
          localStorage.setItem('checkDomesticDocView', JSON.stringify(this.checkDomesticDocView));
        }
      }
    }
    row.dob = new Date(row.dob);
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '60%';
    dialogConfig.height = '85%',
    dialogConfig.data = row;
    const dialogRef = this.dialog.open(OnBoardingViewComponent, dialogConfig);
    dialogRef.componentInstance.onBoardingViewForm.setValue(row);
    dialogRef.afterClosed().subscribe(() => {
      const array = localStorage.getItem('studentsList');
      console.log(JSON.parse(array));
      this.listData = new MatTableDataSource(JSON.parse(array));
      this.listData._updateChangeSubscription();
      if (this.listData.data !== null) {
        this.listData.sort = this.sort;
        this.listData.paginator = this.paginator;
        this.listData.filterPredicate = (data, filter) => {
          return this.displayedColumns.some(ele => {
            return ele !== 'actions' && data[ele].toString().toLowerCase().indexOf(filter) !== -1;
          });
        };
      }
    });
  }

  onDelete(row) {
    let popupReturnvalue = false;
    console.log(row);
    this.dialogService.openConfirmDialog('Are you sure to delete this record?')
      .afterClosed().subscribe(res => {
        console.log(res);
        popupReturnvalue = res;
        if (popupReturnvalue) {
          const array = localStorage.getItem('studentsList');
          console.log(JSON.parse(array));
          this.listData = new MatTableDataSource(JSON.parse(array));
          const index = this.listData.data.findIndex(item => item.id === row.id);
          this.listData.data.splice(index, 1);
          localStorage.studentsList = JSON.stringify(this.listData.data);
          this.listData._updateChangeSubscription();
          if (this.listData.data !== null) {
            this.listData.sort = this.sort;
            this.listData.paginator = this.paginator;
            this.listData.filterPredicate = (data, filter) => {
              return this.displayedColumns.some(ele => {
                return ele !== 'actions' && data[ele].toString().toLowerCase().indexOf(filter) !== -1;
              });
            };
          }
        }
      });
  }
}
